import { useCallback, useEffect, useRef, useState } from 'preact/hooks';
import { useText } from '@adobe-commerce/elsie/i18n';
import { FieldsProps, FormValues, useFormProps } from '@/order/types';

const initializeFormData = (
  fieldsConfig: Array<{
    code: string;
    required: boolean;
    defaultValue: unknown;
  }>
): Record<string, unknown> => {
  return fieldsConfig.reduce(
    (obj: Record<string, unknown>, { code, required, defaultValue }) => {
      if (required) obj[code] = defaultValue;
      return obj;
    },
    {}
  );
};

export const useForm = ({ fieldsConfig, onSubmit }: useFormProps) => {
  const { requiredFieldError } = useText({
    requiredFieldError: 'Order.Form.notifications.requiredFieldError',
  });

  const formRef = useRef<HTMLFormElement>(null);
  const [formData, setFormData] = useState<FormValues>({});
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    setFormData({});
    if (!fieldsConfig || !fieldsConfig.length) return;

    const initialData = initializeFormData(fieldsConfig);
    setFormData(initialData as FormValues);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fieldsConfig?.length]);

  useEffect(() => {
    return () => {
      setFormData({});
      // eslint-disable-next-line
      formRef.current?.reset();
    };
  }, []);

  const getRequiredFieldError = useCallback(
    (name: string, value: any) => {
      const error = (fieldsConfig as FieldsProps[]).find(
        (el) => el.code === name
      );

      const renderErrorMessage =
        error?.required && !value ? requiredFieldError : '';

      return renderErrorMessage;
    },
    [fieldsConfig, requiredFieldError]
  );

  const handleChange = useCallback((event: Event) => {
    const { name, value, type, checked } = event?.target as HTMLInputElement;
    const fieldValue = type === 'checkbox' ? checked : value;

    setFormData((prev) => ({ ...prev, [name]: fieldValue }));
  }, []);

  const handleBlur = useCallback(
    (event: Event) => {
      const { name, value, type, checked } = event?.target as HTMLInputElement;
      const fieldValue = type === 'checkbox' ? checked : value;

      setErrors((prevErrors) => ({
        ...prevErrors,
        [name]: getRequiredFieldError(name, fieldValue),
      }));
    },
    [getRequiredFieldError]
  );

  const handleSubmit = useCallback(
    (event: SubmitEvent) => {
      event.preventDefault();

      let formValid = true;
      let newErrors: Record<string, string> = {};
      let firstErrorField: string | null = null;

      for (const [name, value] of Object.entries(formData)) {
        const error = getRequiredFieldError(name, value);
        if (error) {
          newErrors[name] = error;
          formValid = false;
          if (!firstErrorField) {
            firstErrorField = name;
          }
        }
      }

      setErrors(newErrors);

      if (firstErrorField && formRef.current) {
        const input = formRef.current.elements.namedItem(
          firstErrorField
        ) as HTMLElement;
        input?.focus();
      }

      onSubmit?.(event, formValid);
    },
    [formData, getRequiredFieldError, onSubmit]
  );

  return {
    formData,
    errors,
    formRef,
    handleChange,
    handleBlur,
    handleSubmit,
  };
};
