import { FunctionComponent } from 'preact';
import { useForm } from '@/order/hooks/components/useForm';
import { FormProps } from '@/order/types';
import FormInputs from './FormInputs';
import { memo } from 'preact/compat';
import { classes } from '@adobe-commerce/elsie/lib';

export const Form: FunctionComponent<FormProps> = memo(
  ({
    name,
    loading,
    children,
    className = 'defaultForm',
    fieldsConfig,
    onSubmit,
  }) => {
    const {
      formData,
      errors,
      formRef,
      handleChange,
      handleBlur,
      handleSubmit,
    } = useForm({
      fieldsConfig,
      onSubmit,
    });

    return (
      <form
        className={classes(['order-form', className])}
        onSubmit={handleSubmit}
        name={name}
        ref={formRef}
      >
        <FormInputs
          className={className}
          loading={loading}
          fields={fieldsConfig}
          onChange={handleChange}
          onBlur={handleBlur}
          errors={errors}
          values={formData}
        />
        {children}
      </form>
    );
  }
);
