import { FunctionComponent } from 'preact';
import { classes } from '@adobe-commerce/elsie/lib';
import { memo, useCallback } from 'preact/compat';
import { User } from '@adobe-commerce/elsie/icons';
import {
  Field,
  Input,
  Picker,
  Checkbox,
  InputDate,
  TextArea,
} from '@adobe-commerce/elsie/components';
import { FieldsProps, FormInputsProps } from '@/order/types';

export const FormInputs: FunctionComponent<FormInputsProps> = memo(
  ({
    loading,
    values,
    fields = [],
    errors,
    className = '',
    onChange,
    onBlur,
  }) => {
    const itemClassName = `${className}__item`;

    const selectElement = useCallback(
      (item: any, valueMessage: string, errorMessage: string) => {
        const pickerOptions = item.options.map((option: any) => {
          return { text: option.label, value: option.value };
        });

        return (
          <Field
            key={item.id}
            error={errorMessage}
            className={classes([
              itemClassName,
              `${itemClassName}--${item.id}`,
              [`${itemClassName}--${item.id}-hidden`, item.is_hidden],
              item.className,
            ])}
            data-testid={`${className}--${item.id}`}
            disabled={loading}
          >
            <Picker
              name={item.id}
              floatingLabel={`${item.label} ${item.required ? '*' : ''}`}
              placeholder={item.label!}
              aria-label={item.label!}
              options={pickerOptions}
              onBlur={onBlur}
              handleSelect={onChange}
              value={valueMessage || item.defaultValue}
            />
          </Field>
        );
      },
      [className, loading, itemClassName, onBlur, onChange]
    );

    const inputElement = useCallback(
      (item: any, valueMessage: string, errorMessage: string) => {
        const isEmail = item.id === 'email';
        const icon = isEmail ? <User /> : undefined;
        const autoComplete = isEmail ? 'username' : '';

        return (
          <Field
            key={item.id}
            error={errorMessage}
            className={classes([
              itemClassName,
              `${itemClassName}--${item.id}`,
              [`${itemClassName}--${item.id}-hidden`, item?.is_hidden],
              item.className,
            ])}
            data-testid={`${className}--${item.id}`}
            disabled={loading}
          >
            <Input
              aria-label={item.label}
              aria-required={item.required}
              autoComplete={autoComplete}
              icon={icon}
              type="text"
              name={item.id}
              value={valueMessage || item.defaultValue}
              placeholder={item.label}
              floatingLabel={`${item.label} ${item.required ? '*' : ''}`}
              onBlur={onBlur}
              onChange={onChange}
            />
          </Field>
        );
      },
      [className, loading, itemClassName, onBlur, onChange]
    );

    const inputDateElement = useCallback(
      (item: any, valueMessage: string, errorMessage: string) => {
        return (
          <Field
            key={item.id}
            error={errorMessage}
            className={classes([
              itemClassName,
              `${itemClassName}--${item.id}`,
              [`${itemClassName}--${item.id}-hidden`, item.is_hidden],
              item.className,
            ])}
            data-testid={`${className}--${item.id}`}
            disabled={loading}
          >
            <InputDate
              type="text"
              name={item.id}
              value={valueMessage || item.defaultValue}
              placeholder={item.label}
              floatingLabel={`${item.label} ${item.required ? '*' : ''}`}
              onBlur={onBlur}
              onChange={onChange}
            />
          </Field>
        );
      },
      [className, loading, itemClassName, onBlur, onChange]
    );

    const inputCheckBoxElement = useCallback(
      (item: any, valueMessage: string, errorMessage: string) => {
        return (
          <Field
            key={item.id}
            error={errorMessage}
            className={classes([
              itemClassName,
              `${itemClassName}--${item.id}`,
              [`${itemClassName}--${item.id}-hidden`, item.is_hidden],
              item.className,
            ])}
            data-testid={`${className}--${item.id}`}
            disabled={loading}
          >
            <Checkbox
              name={item.id}
              checked={valueMessage || item.defaultValue}
              placeholder={item.label}
              label={`${item.label} ${item.required ? '*' : ''}`}
              onBlur={onBlur}
              onChange={onChange}
            />
          </Field>
        );
      },
      [className, loading, itemClassName, onBlur, onChange]
    );

    const textAreaElement = useCallback(
      (item: any, valueMessage: string, errorMessage: string) => {
        return (
          <Field
            key={item.id}
            error={errorMessage}
            className={classes([
              itemClassName,
              `${itemClassName}--${item.id}`,
              [`${itemClassName}--${item.id}-hidden`, item.is_hidden],
              item.className,
            ])}
            data-testid={`${className}--${item.id}`}
            disabled={loading}
          >
            <TextArea
              type="text"
              name={item.id}
              value={
                valueMessage === undefined ? item.defaultValue : valueMessage
              }
              label={`${item.label} ${item.required ? '*' : ''}`}
              onBlur={onBlur}
              onChange={onChange}
            />
          </Field>
        );
      },
      [className, loading, itemClassName, onBlur, onChange]
    );

    if (!fields.length) return null;

    return (
      <>
        {fields.map((item: FieldsProps) => {
          const errorMessage = (errors?.[item.id] as string) ?? '';
          const valueMessage = (values?.[item.id] as string) ?? '';

          switch (item.fieldType) {
            case 'TEXT': {
              if (item?.options?.length) {
                return selectElement(item, valueMessage, errorMessage);
              }

              return inputElement(item, valueMessage, errorMessage);
            }

            case 'MULTILINE': {
              return inputElement(item, valueMessage, errorMessage);
            }

            case 'SELECT': {
              return selectElement(item, valueMessage, errorMessage);
            }

            case 'DATE': {
              return inputDateElement(item, valueMessage, errorMessage);
            }

            case 'BOOLEAN': {
              return inputCheckBoxElement(item, valueMessage, errorMessage);
            }

            case 'TEXTAREA': {
              return textAreaElement(item, valueMessage, errorMessage);
            }

            default:
              return null;
          }
        })}
      </>
    );
  }
);
